import React, {useRef} from 'react'
import { useDetectOutsideClick } from "./useDetectOutsideClick";

function DropdownMenu() {
  let dropdownRef = useRef(null)
  let [isActive, setIsActive] = useDetectOutsideClick(dropdownRef, false)
  let onMenuClick = () => setIsActive(!isActive)

  return (
    <div className="menu-container">
      <button onClick={onMenuClick} className="menu-trigger">
        <i class="fas fa-bars"></i>
      </button>
      <nav ref={dropdownRef}
           className={`menu ${isActive ? 'active' : 'inactive'}`}
      >
        <ul>
          <li><a href="#about">About</a></li>
          <li><a href="#project-title">Previous Work</a></li>
          <li><a href="https://github.com/zxum">Github</a></li>
          <li><a href="http://www.linkedin.com/in/zionum">LinkedIn</a></li>
        </ul>
      </nav>
    </div>
  )
}

export default DropdownMenu
