import React from 'react'
import Tag from './Tag'

function Project(props) {
  console.log(props)
  
  let backgroundStyle = {
    backgroundImage: `url(${process.env.PUBLIC_URL + "/images/" + props.gif})`
  }

  let tagList = props.tags.map(tag => {
    return <Tag tagname={tag} />
  })

  return (
    <div className='project-card'>
      <div className="project-img" style={backgroundStyle}></div>
      <h2 className="title">{props.title}</h2>
      <div className="tags-area">
        {tagList}
      </div>
      <div className="card-links">
        <div className="github-link"><a href={props.github}>Github</a></div>
        <div className="livedemo-link"><a href={props.livedemo}>Live Demo</a></div>
      </div>
    </div>
  )
}

export default Project
