import React from 'react'
import Project from './Project'
import catalog from '../assets/projects-list.json'

function Projects(props) {

  let projectNamesArr = Object.keys(catalog)
  // console.log(projectNamesArr)
  let projectList = projectNamesArr.map(projectName => {
    let project = catalog[projectName]
    // console.log(project)
    return (<Project key={project.id}
                    github={project.github}
                    livedemo={project.livedemo}
                    gif={project.gif}
                    tags={project.tags}
                    title={project.title}
                    name={projectName}
    />) 
  })

  return (
    <div id='projects-container'>
      {projectList}
    </div>
  )
}

export default Projects
